.place {
    color: #000000;
    font-family: 'Inter', Arial, sans-serif;
    display: flex;
    flex-direction: column;
    margin: 0;
    border-radius: 10px;
    overflow: hidden; /* чтобы убрать белые уголки от border-radius */
    box-sizing: border-box;
    position: relative;
}
