@import url('./__image/profile__image.css');
@import url(./__info/profile__info.css);
@import url(./__name-wrap/profile__name-wrap.css);
@import url('./__name/profile__name.css');
@import url(./__job/profile__job.css);
@import url(./__edit-button/profile__edit-button.css);
@import url(./__add-button/profile__add-button.css);

.profile {
    font-family: 'Inter', Arial, sans-serif;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 50px;
}

@media screen and (max-width: 900px) {
    .profile {
        flex-direction: column;
        margin-bottom: 36px;
    }
}
