.page__container {
    min-width: 282px;
    max-width: 882px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    overflow: hidden;
    padding: 40px 70px 40px;
    box-sizing: border-box;
}

@media screen and (max-width: 500px) {
    .page__container {
        padding: 28px 19px 36px;
    }
}
